              <el-option label=" & " value="Altitude & Speed"></el-option>
              <el-option label=" & " value="To & From"></el-option>
              <el-option label="" value="Type"></el-option>
              <el-option label="" value="Altitude"></el-option>
              <el-option label="" value="Speed"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="form.label4">
              <el-option label="" value="Callsign"></el-option>
              <el-option label="" value="Registration"></el-option>
              <el-option label="" value="Flight Number"></el-option>
              <el-option label=" & " value="Type & Registration"></el-option>
              <el-option label=" & " value="Altitude & Speed"></el-option>
              <el-option label=" & " value="To & From"></el-option>
              <el-option label="" value="Type"></el-option>
              <el-option label="" value="Altitude"></el-option>
              <el-option label="" value="Speed"></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="">
