          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="200px">
          <el-switch
            v-model="form.Intense"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.winds">
            <el-option label="Off" value="Off"></el-option>
            <el-option label="On" value="On"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-switch
            v-model="form.Lightning"
