            inactive-color="#eee">
          </el-switch>
        </el-form-item>
      </el-form>
    </div>
    <div class="pane" v-if="this.nowActive === ''">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item class="form_item head" label="" label-width="200px"></el-form-item>
        <el-form-item class="form_item" label="ADS-B (Live)">
          <el-switch
            v-model="form.adsb"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="MLAT (Live)">
          <el-switch
            v-model="form.mlat"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="FLARM (Live)">
