        <el-form-item class="form_item" label="/" label-width="200px">
          <el-switch
            v-model="form.radar"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.estimations">
            <el-option label="" value="Off"></el-option>
            <el-option label="5 " value="5 mins"></el-option>
            <el-option label="15 " value="15 mins"></el-option>
            <el-option label="30 " value="30 mins"></el-option>
            <el-option label="60 " value="60 mins"></el-option>
            <el-option label="120 " value="120 mins"></el-option>
            <el-option label="240 " value="240 mins"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="form_item head" label="" label-width="200px"></el-form-item>
        <el-form-item class="form_item" label="">
          <el-switch
            v-model="form.airborne"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="200px">
          <el-switch
            v-model="form.ground"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="form.vehicles"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="form.gliders"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
      </el-form>
    </div>
    <div class="pane" v-if="this.nowActive === ''">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item class="form_item" label="" label-width="220px">
          <el-switch
            v-model="form.showLine"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="220px">
          <el-switch
            v-model="form.showLine"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="220px">
          <el-switch
            v-model="form.showLine"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="220px">
          <el-switch
            v-model="form.showLine"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="" label-width="220px">
          <el-switch
            v-model="form.showLine"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
