        <el-form-item class="form_item" label="">
          <el-switch
            v-model="form.animate"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item label="12">
          <el-switch
            v-model="form.labels"
            active-color="#f8c023"
            inactive-color="#eee">
          </el-switch>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.temperature">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.aircraftSpeed">
            <el-option label="" value="Knots"></el-option>
            <el-option label="/" value="Km/h"></el-option>
            <el-option label="" value="Mph"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.verticalSpeed">
            <el-option label="Fpm" value="Fpm"></el-option>
            <el-option label="/" value="M/s"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="form_item" label="">
          <el-select v-model="form.windSpeed">
            <el-option label="" value="Knots"></el-option>
            <el-option label="/" value="Km/h"></el-option>
            <el-option label="" value="Mph"></el-option>
            <el-option label="" value="M/s"></el-option>
          </el-select>
