        <el-form-item class="form_item" label="">
          <el-select v-model="form.altitude">
            <el-option label="" value="Feet"></el-option>
            <el-option label="" value="Meters"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.distance">
            <el-option label="" value="Km"></el-option>
            <el-option label="" value="Miles"></el-option>
            <el-option label="" value="Nm"></el-option>
