        </el-form-item>
      </el-form>
    </div>
    <div class="foot">
      <span></span>
    </div>
  </div>
</template>

<script>
  import DragLine from "./dragLine.vue"

  export default {
    data() {
      return {
        navList: [
          {
            name: "",
            active: true,
          },
          {
            name: "",
            active: false,
          },
          {
            name: "",
            active: false,
          },
          {
            name: "",
            active: false,
          },
        ],
        form: {
          name: 'Terrain',
          region: 'Roadmap',
          flightLayer: true,
          trackerLayer: true,
          charts: 'None',
          size: 'Auto',
          label1: '',
          label2: '',
          label3: ' & ',
          label4: '',
          winds: 'Off',
          estimations: '240 mins',
          temperature: '',
          aircraftSpeed: 'Knots',
          verticalSpeed: 'Fpm',
          windSpeed: 'Knots',
          altitude: 'Feet',
          distance: 'Km',
          showLine: false,
          atc: false,
          tracks: false,
        },
        nowActive: "",
      }
    },

    components: {
      DragLine,
    },

    methods: {
      changeActive(name) {
        this.nowActive = name
        this.navList.forEach(item => {
          if(name === item.name) {
            item.active = true
          }else {
            item.active = false
          }
        })
      },

      changeMapType(val) {
        this.$emit("changeMapType", val)
      },

      toggleFlightLayer(state) {
        this.$emit("toggleFlightLayer", state)
      },

      toggleTrackerLayer(state) {
        this.$emit("toggleTrackerLayer", state)
      }
    },
  }
</script>

<style lang="less">
  .MapSetting {
    position: absolute;
    top: 130px;
    right: 45px;
    width: 320px;
    z-index: 2000;

    .nav {
      height: 40px;
      background: #000;
      .nav_item {
        display: inline-block;
        height: 40px;
        padding: 0 15px;
        color: #fff;
        line-height: 40px;
        font-size: 14px;
        cursor: pointer;
      }
      .nav_item.active {
        background: #222121;
        color: #f8c023;
      }
    }

    .pane {
      background: rgba(0,0,0,0.7);
      padding: 0 10px;
      height: 380px;
      overflow-y: auto;
      .el-form {
        .form_item {
          border-bottom: 1px solid #656464;
        }

        .head {
          .el-form-item__label {
            color: #f8c023 !important;
          }
        }

        .el-form-item {
          height: 35px;
          margin-bottom: 0px;
          overflow: hidden;
          box-sizing: border-box;

          .el-form-item__label {
            text-align: left;
            color: #fff;
            font-size: 14px;
            line-height: 34px;
          }

          .el-form-item__content {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            height: 34px;
            .el-input {
              input {
                height: 24px;
                width: 170px;
                border-radius: 0px;
              }
            }
            .el-switch {
              width: 45px;
              height: 20px;
            }

            .el-input__inner {
              font-size: 12px !important;
            }
          }
        }

        .labels_wrap {
          position: relative;
          img {
            position: absolute;
            width: 110px;
            height: 135px;
          }
        }
      }
    }

    .foot {
      height: 40px;
      background: #000;
      text-align: center;
      line-height: 40px;
      color: #64bef1;

      span {
        cursor: pointer;
        text-decoration: underline;
      }
    }
  }
</style>



