<template>
  <div id="app">
    <div id="content">
      <div class="section">
        <h3></h3>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="date" label="/" width="180">
          </el-table-column>
          <el-table-column prop="flight_no" label="" width="180">
          </el-table-column>
          <el-table-column prop="departure" label="" width="180">
          </el-table-column>
          <el-table-column prop="aviation" label="">
          </el-table-column>
          <el-table-column prop="aircraft" label="" width="180">
          </el-table-column>
          <el-table-column prop="latlng" label="/" width="180">
          </el-table-column>
          <el-table-column prop="status" label="">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          flight_no: "JD3217",
          departure: "",
          aviation: "",
          aircraft: "",
          latlng: "30",
          status: "",
        },
      ],
    }
  },
}
</script>
<style lang="less" scoped>
#app {
  background: #fff;
  width: 100%;

  #content {
    height: 100%;
    padding: 50px 30px;

    .section {
      width: 100%;
      height: 100%;

      h3 {
        color: #000000;
        display: block;
        font-family: "Roboto Condensed";
        font-size: 3.6em;
        font-weight: normal;
        line-height: 0.8em;
        margin-bottom: 10px;
      }
    }
  }
}
</style>




